}

        .border {
            border: 1px solid green;
            min-height: 500px;
            height: 500px;
			width: 95%;
			transition:0.5s;
        }
		
		.border:hover {
			border: 1px solid #0070f0;
            min-height: 500px;
            height: 500px;
			width: 95%;
			transition:0.5s;
		}

